@import url('https://fonts.googleapis.com/css2?family=Inter:wght@200;300;400;500;600;700;800;900&family=Work+Sans&display=swap');

html{
    scroll-behavior: smooth;
}
[data-theme="dark"] {
    --background: #111111;
    --accent-color1: #000000;
    --accent-color2: #eeeeee;
    --accent-color3: #777777;
    --card-color: #eeeeee;
    --accent-color4: #555555;
}
.main-body{
    width: 100%;
    background-color: var(--background);
    transition: .5s ease-in-out;
}
.body{
    display: flex;
    flex-direction: column;
    width: 70%;
    margin: auto;
    @media screen and (max-width: 1024px) {
        width: 90%;
    }
    // @media screen and (max-width: 512px) {
    //     width: 100%;
    // }
}


// NAVBAR..........................................................


navbar{
    height: 9vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .logo{
        a{
            text-decoration: none;
            color: blueviolet;
        }
        @media screen and (max-width: 512px) {
            padding-left: .7rem;
        }
    }
    .nav-menu{
        display: flex;
        list-style-type: none;
        gap: 2rem;
        margin-bottom: 0;
        .nav-items{
            a{
                text-decoration: none;
                color: var(--accent-color2);
                font-size: 1.1rem;
                font-weight: 500;
                @media screen and (max-width: 800px) {
                    font-size: 1.4rem;
                }
            }
        }
        @media screen and (max-width: 800px) {
            display: flex;
            flex-direction: column;
            position: absolute;
            background-color: aquamarine;
            top: 0;
            right: 0;
            height: 100vh;
            width: 40vw;
            justify-content: center;
            text-align: center;
            padding: 0;
            visibility: hidden;
        }
        .fa-sun{
            font-size: 1.5rem;
            cursor: pointer;
            @media screen and (max-width: 800px) {
                display: none;
            }
        }
        .fa-moon{
            font-size: 1.5rem;
            cursor: pointer;
            @media screen and (max-width: 800px) {
                display: none;
            }
        }
    }
    .nav-menu.active{
        right: 0;
        transition: .7s ease-in;
        visibility: visible;
    }
    .icons{
        display: none;
        @media screen and (max-width: 800px) {
            display: block;
            padding-bottom: 1rem;
            font-size: 1.9rem;
            font-weight: lighter;
            z-index: 10;
        }
        @media screen and (max-width: 512px) {
            padding-right: .4rem;
        }
    }
    .wrapper{
        display: flex;
        .sun-moon{
            display: none;
            @media screen and (max-width: 800px) {
                display: block;
                cursor: pointer;
                margin-right: 1rem;
                padding-top: 1rem;   
            }
            .fa-sun{
                font-size: 1.4rem;
            }
            .fa-moon{
                font-size: 1.4rem;
            }
        }
    }
}


// HOME....................................................................



#home{
    .page{
        .home-content{
            display: flex;
            justify-content: space-between;
            .left{
                h3{
                    padding-top: 15rem;
                    font-size: 2.4rem;
                    line-height: 3rem;
                    font-weight: 400;
                    color: var(--accent-color2);
                    span{
                        color: skyblue;
                        font-size: 2.8rem;
                    }
                    @media screen and (max-width: 768px) {
                        padding-top: 5rem;
                    }
                    @media screen and (max-width: 450px) {
                        font-size: 2.2rem;
                    }
                }
            }
            .right{
                padding-top: 7rem;
                img{
                    height: 380px;
                    @media screen and (max-width: 450px) {
                        height: 360px;
                    }
                }
                @media screen and (max-width: 768px) {
                    padding-top: 1rem;
                }
            }
            @media screen and (max-width: 768px) {
                flex-direction: column;
                justify-content: center;
                text-align: center;
            }
        }
        .icons{
            display: flex;
            justify-content: center;
            padding: 6px;
            margin: 6px;
            gap: 1rem;
            img{
                height: 60px;
                @media screen and (max-width: 450px) {
                    height: 40px;
                }
            }
            @media screen and (max-width: 768px) {
                width: 100%;
            }
        }
    }
}


// PROJECTS....................................................................


#projects{
    padding: 3rem 0;
    h4{
        font-size: 2rem;
        color: var(--accent-color2);
    }
    .project-content{
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        @media screen and (max-width: 968px) {
            display: flex;
            flex-direction: column-reverse;
            
        }
        .content{
            @media screen and (max-width: 968px) {
                padding: 2rem 0;
            }
            p{
                font-size: 1.5rem;
                padding: 1.5rem 0;
                font-weight: 500;
                color: var(--accent-color2);
                @media screen and (max-width: 391px) {
                    font-size: 1.2rem;
                }
            }
            .tags{
                padding: 1rem 0;
                a{
                    text-decoration: none;
                    color: black;
                    background-color: aqua;
                    font-size: 1.6rem;
                    margin-right: 1rem;
                    padding: 5px 14px;
                    border-radius: 6px;
                    font-weight: 600;
                    @media screen and (max-width: 391px) {
                        font-size: 1.2rem;
                    }
                }
            }
            .butns{
                button{
                    border: none;
                    background-color: aqua;
                    padding: 5px 14px;
                    border-radius: 6px;
                    font-size: 1.5rem;
                    font-weight: 600;
                    margin-right: 1rem !important;
                    @media screen and (max-width: 391px) {
                        font-size: 1rem;
                        margin-right: .5rem !important;
                    }
                }
            }
        }

        .image{
                text-align: right;
                @media screen and (max-width:968px) {
                    text-align: center;
                }
            img{
                width: 90%;
                border-radius: 1rem;
                @media screen and (max-width:512px) {
                    width: 100%;
                }
            }
        }
    }
}



// SEPRATOR........................................................


.seprator{
    background-image: linear-gradient(to right top,
        #3654e7,
        #5c52e9,
        #784feb,
        #914aeb,
        #a745ea,
        #7d86fb,
        #4d7fff,
        #0091ff,
        #00acfd,
        #00bdd9,
        #00c8a3,
        #6bcc70,
    );
    height: 6px;
    width: 40%;
    border-radius: 3px;
    margin: 16px 0;
}


// SKILLS..........................................................




#skills{
    width: 100%;
    color: var(--accent-color2);
.front-content{
    display: flex;
    justify-content: space-between;
    .left{
        padding-top: 2rem;
    }
    .right{
        display: flex;
        .card-wrapper{
            .card{
                border: none;
                background-color: transparent;
                text-align: center;
                padding-top: 3rem;
                i{
                    font-size: 4rem;
                }
                p{
                    font-size: 1.2rem;
                    font-weight: 500;
                }
            }
            @media screen and (max-width: 768px) {
                width: 100%;
            }
        }
        @media screen and (max-width: 512px) {
            flex-direction: column;
        }
    }
    @media screen and (max-width: 768px) {
        flex-direction: column;
        text-align: center;
    }
}

.back-content{
    display: flex;
    justify-content: space-between;
    .left{
        padding-top: 2rem;
    }
    .right{
        display: flex;
        .card-wrapper{
            .card{
                border: none;
                background-color: transparent;
                text-align: center;
                padding-top: 3rem;
                i{
                    font-size: 4rem;
                }
                p{
                    font-size: 1.2rem;
                    font-weight: 500;
                }
            }
            @media screen and (max-width: 768px) {
                width: 100%;
            }
        }
        @media screen and (max-width: 512px) {
            flex-direction: column;
        }
    }
    @media screen and (max-width: 768px) {
        flex-direction: column;
        text-align: center;
    }
  }
}




// WORK.........................................................



.work{
    margin-bottom: 5rem;
    color: var(--accent-color2);
    .work-wrapper{
        display: grid;
        grid-template-columns: 1fr 1fr;
        padding-top: 2rem;
        gap: 1rem;
        .left{
            padding-bottom: 4rem;
            h6{
                color: grey;
            }
            @media screen and (max-width: 768px) {
                display: flex;
                flex-direction: column-reverse;
            }
            .image{
                @media screen and (max-width: 768px){
                    max-width: 100%;
                    text-align: right;
                }
                img{
                    width: 20%;
                }
            }
        }
        .right{
            h6{
                color: grey;
            }
            @media screen and (max-width: 768px) {
                display: flex;
                flex-direction: column-reverse;
            }
            .image{
                @media screen and (max-width: 768px){
                    max-width: 100%;
                    text-align: right;
                }
                img{
                    width: 20%;
                }
            }
        }
        @media screen and (max-width: 768px) {
            display: flex;
            flex-direction: column;
        }
    }
}


// CONTACT......................................................


.contact{
    padding-bottom: 5rem;
    color: var(--accent-color2);
    .contact-wrapper{
        display: grid;
        grid-template-columns: 1fr 1fr;
        text-align: center;
        padding-top: 2rem;
        gap: 2rem;
        .left{
            p{
                font-size: 1.7rem;
                @media screen and (max-width: 1135px) {
                    font-size: 1.3rem;
                }
            }
            .icons{
                img{
                    height: 60px;
                    margin-left: 1rem;
                }
            }
        }
        .right{
            button{
                border: none;
                background-color: aquamarine;
                padding: 13px 38px;
                font-size: 2rem;
                border-radius: 7px;
                margin-top: 2rem;
                @media screen and (max-width: 1135px) {
                    font-size: 1.7rem;
                }
                @media screen and (max-width: 1135px) {
                    font-size: 1.4rem;
                }
            }
        }
        @media screen and (max-width: 968px) {
            display: flex;
            flex-direction: column;
        }
    }
}



// SCROLL.........................................................................


.scrollTop{
    visibility: hidden;
    opacity: 0;
    position: fixed;
    z-index: 100;
    bottom: 5%;
    right: 5%;
    height: 4rem;
    width: 4rem;
    border: 3px solid transparent;
    border-radius: 3rem;
    background-image: linear-gradient(rgba(255,255,255,0), rgba(255,255,255,0)), linear-gradient(101deg, var(--pink), var(--orange));
    background-origin: border-box;
    background-clip: content-box, border-box;
    box-shadow: 2px 1000px 1px var(--background) inset;
    transition: .5s ease-in-out;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover{
        box-shadow: none;
    }
    a{
        .fa-chevron-up{
            font-size: 2rem;
            color: var(--accent-color2);
        }
    }
}

.view{
    visibility: visible;
    opacity: 1;
}